//== Media Queries
// media query
$breakpoints: (
  'sm': 'screen and (max-width: 540px)',
  'md': 'screen and (max-width: 768px)',
  'lg': 'screen and (max-width: 1024px)',
  'xl': 'screen and (max-width: 1366px)',
) !default;

@mixin mq($breakpoint: md) {
	@media #{map-get($breakpoints, $breakpoint)} {
		@content;
	}
}

// line-height
@mixin lhCrop($line-height) {
  &::before {
    content: '';
    display: block;
    height: 0;
    width: 0;
    margin-top: calc((1 - #{$line-height}) * 0.5em);
  }
}

$black: #000;
$gray: #666;

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline
}
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block
}
ol, ul {
  list-style: none
}
blockquote, q {
  quotes: none
}
blockquote:before, blockquote:after, q:before, q:after {
  content: '';
  content: none
}
table {
  border-collapse: collapse;
  border-spacing: 0
}

html {
	overflow-x:hidden;
	overflow-y:auto;
	font-size: 62.5%;
}

b,
strong {
  color: $black;
  font-weight: 700;
}

body {
	font-family: "Montserrat","Noto Sans JP","Yu Gothic",YuGothic,"ヒラギノ角ゴシック Pro","Hiragino Kaku Gothic Pro","メイリオ",Meiryo,Osaka,"ＭＳ Ｐゴシック","MS PGothic",sans-serif;
	font-size: 10px;
	font-size: 1.0rem;
	line-height: 1.5;
	position: relative;
	color: $gray;
	padding: 0;
	margin: 0;
	background-color: #FFF;
  letter-spacing: normal;
  font-weight: 400;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	-webkit-font-feature-settings: "palt";
	font-feature-settings: "palt";
}

a {
  color: $gray;
  &:hover {
    text-decoration: none;
  }
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 700;
  color: $black;
}

h2 {
  font-size: 3.6rem;
  line-height: 1;
  text-align: center;
  font-family: 'Montserrat', sans-serif;
  color: $black;
  letter-spacing: 1px;
  @include mq(md) {
    font-size: 2rem;
  }
}

#hero {
  img {
    width: 100%;
  }
  .pc {
    display: block;
    @include mq(md) {
      display: none;
    }
  }
  .sp {
    display: none;
    @include mq(md) {
      display: block;
    }
  }
}

.container {
  width: 1440px;
  margin-left: auto;
  margin-right: auto;
  box-sizing: border-box;
  padding: 90px;
  @include mq(xl) {
    width: auto;
    margin-left: 0;
    margin-right: 0;
  }
  @include mq(lg) {
    padding: 60px 24px;
  }
}

#outline {
  #outline-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include mq(lg) {
      display: block;
    }
    #information {
      flex: 1;
      color: $black;
      @include mq(lg) {
        margin-bottom: 50px;
      }
      li {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        line-height: 1;
        margin-bottom: 30px;
        @include mq(lg) {
          margin-bottom: 15px;
        }
        &:last-child {
          margin-bottom: 0;
        }
        i {
          font-size: 2.4rem;
          margin-right: 20px;
        }
        &.date {
          i {
            font-size: 2rem;
          }
        }
        date {
          font-size: 5.2rem;
          display: flex;
          align-items: center;
          justify-content: flex-start;
          @include mq(lg) {
            font-size: 1.8rem;
          }
          span {
            font-weight: 600;
            letter-spacing: 4px;
            @include mq(lg) {
              letter-spacing: 2px;
            }
          }
          small {
            position: relative;
            top: 3px;
            font-weight: 700;
            font-size: 2.4rem;
            @include mq(lg) {
              font-size: 1.1rem;
            }
          }
        }
        time {
          font-size: 2.8rem;
          font-weight: 700;
          @include mq(lg) {
            font-size: 1.8rem;
          }
        }
        .method {
          font-size: 1.8rem;
          font-weight: 700;
          @include mq(lg) {
            font-size: 1.2rem;
          }
        }
      }
    }
    #outline-description {
      flex-basis: 800px;
      font-size: 1.6rem;
      color: $black;
      margin-left: 90px;
      @include mq(lg) {
        margin-left: 0;
        font-size: 1.3rem;
      }
      p {
        margin-bottom: 1.5em;
        &:last-of-type {
          margin-bottom: 0;
        }
      }
    }
  }
}


// SPEAKERS
#speakers {
  background-color: #1F1D21;
  color: #FFF;
  h2 {
    color: #FFF;
    margin-bottom: 90px;
    @include mq(lg) {
      display: block;
      margin-bottom: 60px;
    }
  }
}

.speaker {
  .img {
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
  h3 {
    color: #FFF;
  }
}

#pickup-speakers {
  margin-bottom: 50px;
  .speaker {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 50px;
    @include mq(md) {
      display: block;
      margin-bottom: 30px;
    }
    .img {
      flex-basis: 48.8095238095238%;
      img {
        max-width: 100%;
      }
    }
    figcaption {
      margin-left: 3.968253968254%;
      flex: 1;
      @include mq(md) {
        margin-top: 30px;
        margin-left: 0;
      }
      h3 {
        font-size: 2.8rem;
        margin-bottom: 50px;
        @include mq(md) {
          margin-bottom: 30px;
          font-size: 1.6rem;
        }
      }
      ul {
        li {
          font-size: 1.6rem;
          list-style: none;
          @include mq(md) {
            font-size: 1rem;
          }
        }
      }
      .position {
        margin-bottom: 1.5em;
      }
      .profile {
        > * {
          margin-bottom: 1.5em;
          &:last-of-type {
            margin-bottom: 0;
          }
        }
      }
      p {
        font-size: 1.6rem;
        @include mq(md) {
          font-size: 1rem;
        }
      }
    }
  }
}

#normal-speakers {
  display: flex;
  justify-content: flex-start;
  align-items: stretch;
  flex-wrap: wrap;
  margin-left: -1.984126984127%;
  margin-right: -1.984126984127%;
  box-sizing: border-box;
  @include mq(md) {
    margin-left: -12px;
    margin-right: -12px;
  }
  .speaker {
    box-sizing: border-box;
    flex-basis: 25%;
    padding-left: 1.984126984127%;
    padding-right: 1.984126984127%;
    margin-bottom: 50px;
    @include mq(md) {
      flex-basis: 50%;
      padding-left: 12px;
      padding-right: 12px;
    }
    .img {
      img {
        max-width: 100%;
      }
    }
    figcaption {
      flex: 1;
      margin-top: 20px;
      h3 {
        font-size: 1.8rem;
        margin-bottom: 10px;
        @include mq(md) {
          font-size: 1.4rem;
        }
      }
      ul {
        li {
          font-size: 1.4rem;
          list-style: none;
          @include mq(md) {
            font-size: 1rem;
          }
        }
      }
      .more {
        margin-top: 20px;
        a {
          font-size: 1.4rem;
          color: #FFF;
          vertical-align: middle;
          display: inline-flex;
          align-items: center;
          @include mq(md) {
            font-size: 1rem;
          }
          i {
            font-size: 1.8rem;
            @include mq(md) {
              font-size: 1.4rem;
            }
          }
          span {
            margin-left: 6px;
            text-decoration: underline;
          }
        }
      }
    }
  }
}


// program

#program {
  background-color: #EDEFF0;
}
#program-container {
  margin-top: 90px;
  background-color: #FFF;
  border: 1px solid #CCC;
  border-radius: 10px;
  @include mq(lg) {
    margin-top: 60px;
  }
  .program {
    padding: 50px;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    border-bottom: 1px solid #CCC;
    @include mq(lg) {
      display: block;
      padding: 24px;
    }
    .time {
      line-height: 1;
      flex-basis: 208px;
      @include mq(lg) {
        margin-bottom: 24px;
      }
      time {
        background-color: #F6F4F3;
        padding: 5px 10px;
        border-radius: 7.5px;
        color: $gray;
        font-size: 1.4rem;
        line-height: 1;
        @include mq(lg) {
          border-radius: 3px;
          font-size: 1rem;
        }
      }
    }
    .detail {
      flex: 1;

      h3 {
        font-size: 2rem;
        font-weight: 600;
        @include lhCrop(1.5);
        @include mq(lg) {
          font-size: 1.4rem;
        }
      }
      .detail-body {
        margin-top: 30px;
        font-size: 1.6rem;
        @include lhCrop(1.5);
        @include mq(lg) {
          font-size: 1rem;
        }
        strong {
          font-weight: 700;
        }
        p {
          margin-bottom: 1.5em;
          &:last-of-type {
            margin-bottom: 0;
          }
        }
      }
    }
  }
}

// ticket
#ticket {
  #ticket-body {
    width: 800px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 90px;
    font-size: 1.4rem;
    @include mq(lg) {
      width: auto;
      margin-top: 60px;
      font-size: 1.2rem;
    }
    p,
    ul {
      margin-bottom: 1.5em;
    }
  }
  #ticket-container {
    display: flex;
    justify-content: center;
    align-items: stretch;
    flex-wrap: wrap;
    margin-left: -2.5%;
    margin-right: -2.5%;
    @include mq(lg) {
      display: block;
      margin-left: 0;
      margin-right: 0;
      padding-top: 30px;
    }
    .ticket {
      margin-top: 90px;
      flex-basis: 33.33333333%;
      box-sizing: border-box;
      padding-left: 2.5%;
      padding-right: 2.5%;
      @include mq(lg) {
        padding-left: 0;
        padding-right: 0;
        margin-top: 30px;
      }
      .ticket-wrapper {
        box-sizing: border-box;
        padding: 40px;
        background-color: #FBFBFB;
        box-shadow: 0px 0px 20px rgba(214, 214, 214, 0.5);
        h3 {
          font-weight: normal;
          font-size: 1.4rem;
          color: #595959;
          margin-bottom: 17px;
          letter-spacing: 0.58px;
        }
        .price {
          font-weight: 700;
          font-size: 3.6rem;
          color: $black;
          letter-spacing: 1.5px;
          margin-bottom: 17px;
          span {
            display: inline-block;
            vertical-align: middle;
            &:last-of-type {
              margin-left: 12px;
            }
          }
        }
        .description {
          font-size: 1.4rem;
          margin-bottom: 17px;
        }
        .more {
          box-sizing: border-box;
          a {
            display: inline-block;
            background-color: #FF2500;
            color: #FFF;
            font-size: 1.6rem;
            font-weight: 600;
            padding: 16px;
            border-radius: 8px;
            letter-spacing: 2.5px;
            box-sizing: border-box;
          }
        }
      }
    }
  }
}

#sponsor {
  background-color: #EDEFF0;
  h2 {
    font-size: 2rem;
    text-align: center;
    line-height: 1;
    @include mq(lg) {
      font-size: 1.4rem;
    }
  }
  .sponsor {
    background-color: #fff;
    padding: 50px;
    margin-bottom: 10px;
    @include mq(lg) {
      padding: 24px;
    }
    &:last-of-type {
      margin-bottom: 0;
    }
    .sponsor-container {
      display: flex;
      align-items: stretch;
      justify-content: center;
      flex-wrap: wrap;
      figure {
        box-sizing: border-box;
        border-right: 1px solid #EDEFF0;
        flex-basis: 20%;
        margin-top: 50px;
        @include mq(lg) {
          flex-basis: 50%;
          border-right: none;
          margin-top: 24px;
        }
        &:nth-of-type(5n) {
          border-right: none;
        }
        img {
          margin-left: auto;
          margin-right: auto;
          max-width: 150px;
          display: block;
          @include mq(lg) {
            max-width: 100px;
          }
        }
        figcaption {
          margin-top: 15px;
          font-size: 1.4rem;
          color: $gray;
          text-align: center;
        }
      }
    }
  }
}

#contact {
  background-color: #1F1D21;
  color: #CCC;

  .container {
    display: flex;
    align-items: stretch;
    justify-content: space-between;
    @include mq(lg) {
      display: block;
    }
    > div {
      flex-basis: 50%;
    }
  }
  .contact-left {
    font-size: 1.4rem;
    @include mq(lg) {
      font-size: 1.2rem;
    }
    h2 {
      font-size: 2rem;
      color: #FFF;
      text-align: left;
      margin-bottom: 30px;
      small {
        margin-left: 20px;
        display: inline-block;
        font-size: 1.4rem;
        font-weight: 400;
        @include mq(lg) {
          font-size: 1rem;
        }
      }
    }
  }
  .contact-right {
    font-size: 1.6rem;
    color: #FFF;
    @include mq(lg) {
      margin-top: 30px;
      font-size: 1.4rem;
    }
    p {
      margin-bottom: 1.5em;
      &:last-of-type {
        margin-bottom: 0;
      }
    }
    strong {
      color: #FFF;
    }
    a {
      color: #FF2500;
      text-decoration: underline;
    }
  }
}


#footer {
  background-color: #FF2500;
  .container {
    padding: 50px 24px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include mq(lg) {
      display: block;
      padding-top: 50px;
      padding-bottom: 50px;
    }
    #logo {
      svg {
        width: 50px;
      }
      @include mq(lg) {
        text-align: center;
      }
    }
    #footer-nav {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      @include mq(lg) {
        margin-top: 30px;
        flex-direction: column;
        justify-content: center;
      }
      a {
        line-height: 1;
        font-size: 1.4rem;
        color: #FFF;
        font-weight: 600;
        margin-left: 40px;
        @include mq(lg) {
          font-size: 1rem;
          margin-bottom: 12px;
          margin-left: 0;
          &:last-of-type {
            margin-bottom: 0;
          }
        }
      }
    }
  }
}


#modal-close {
  width: 800px;
  margin-left: auto;
  margin-right: auto;
  text-align: right;
  @include mq(lg) {
    width: auto;
  }
}
.uk-modal-close {
  color: #FFF;
  @include mq(lg) {
    color: $black;
  }
  i {
    font-size: 6rem;
    @include mq(lg) {
      font-size: 4rem;
    }
  }
}

.uk-modal-dialog.uk-modal-body {
  width: 800px;
  box-sizing: border-box;
  padding: 80px;
  @include mq(lg) {
    width: auto;
    padding: 24px;
    background-color: transparent;
  }
  .modal-contents {
    display: flex;
    @include mq(lg) {
      display: block;
    }
  }
  .speaker {
    flex-basis: 150px;
    @include mq(lg) {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    .img {
      margin-bottom: 20px;
      @include mq(lg) {
        flex-basis: 150px;
        margin-bottom: 0;
      }
    }
    figcaption {
      @include mq(lg) {
        margin-left: 20px;
        flex: 1;
      }
      h3 {
        color: $black;
        font-size: 1.4rem;
        font-weight: 600;
        margin-bottom: 10px;
      }
      li {
        color: $black;
      }
    }
  }
  .profile {
    color: $black;
    flex: 1;
    margin-left: 80px;
    font-size: 1.2rem;
    @include mq(lg) {
      margin-top: 24px;
      margin-left: 0;
      font-size: 1rem;
    }
    p {
      margin-bottom: 1.5em;
      &:last-of-type {
        margin-bottom: 0;
      }
    }
  }
}



.uk-modal {
  background: rgba(0,0,0,0.8);
  @include mq(lg) {
    background: rgba(255,255,255,0.95);
  }
}
